import React from "react"
import { useFeatureAtom } from "@rp/react/web/router"
import { FilesPage } from "./FilesPage"
import { FilesPageV2 } from "./FilesV2/FilesPageV2"

function FilesPageSelector() {
  const [enableFilePageV2] = useFeatureAtom("enableFilePageV2")

  if (enableFilePageV2) {
    return <FilesPageV2 />
  }

  return <FilesPage />
}

export { FilesPageSelector }
